
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: "center",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
loading
},
data() {
return {
redPacketInfo: "", //领取红包金额
isShowModalRedPack: false, //收益弹窗
redpackBg: "", //红包背景
popName: "",
closeimg: require("@/assets/images/home/close.png"), //close图标
isShowModalRedPack2: false, //领取弹窗
closeModal365: false, //365弹窗
fanli365Obj: {}, //365数据
redmessageInfo: "", //个人中心数据
showLoding: false,
params: {
plat: 3
},
successTipTXT:false
};
},
created() {
console.log(this.$route.query)
console.log(this.user)
console.log("user", this.user.user_type);
console.log("user", this.user);
console.log("openid", this.user.openid);
console.log(this)
if(this.$route.query.url){

console.log(this.$route.query)
let query = {};
for(let i in this.$route.query){
if(i == 'url')continue;
query[i] = this.$route.query[i];
}
this.$router.push({
path: this.$route.query.url,
query
})
return
}
if (this.token) {
this.newredmessage();

this.getredpacketinfo();
}
},
methods: {
searchMoreFans(){
this.$router.push('/centerFull/partner/inviteFansMy365');
},

async newredmessage() {
let that = this;
this.showLoding = true;
let data = {
account: that.account,
token: that.token
};
data = Object.assign(this.params, data);
const [err, res] = await api.newredmessage(data);
if (err) {
that.showLoding = false;
that.$vux.toast.text(err.msg);
return;
}
this.showLoding = false;
this.redmessageInfo = res.data;
this.updateCenter(JSON.stringify(res.data));
},

async newgetorderlist_num_info() {
let data = {};
const [err, res] = api.newgetorderlist_num();
},

linkorderAfter(index){
let url = '';
switch (index) {
case 0:
url = '/centerFull/orderFull/sc_goods';
break;
case 2:
url = '/centerFull/orderFull/after_list';
break;
}
this.$router.push({
path: url
})
},

orderDetail(index) {
let title = this.redmessageInfo.order_status[index].stateTitle || "";
let tabindex = "";
let clickUrl="";
if (!title) {
this.$vux.toast.text("没有权限哦");
return;
}
switch (title) {
case "待付款":
tabindex = 2;
break;
case "待发货":
tabindex = 3;
break;
case "待收货":
tabindex = 4;
break;
case "待评价":
tabindex = 5;
break;
case "全部订单":
tabindex = 1;
break;
case "余额":
tabindex = 6;
break;
case "荟币":
tabindex = 7;
break;
case "积分":
tabindex = 8;
break;
case "我的收益":
tabindex = 9;
clickUrl="centerFull/partner/redenvelope";
break;
case "我的钱包":
tabindex = 10;
clickUrl="/drawIndex"
break;
}
console.log(title)

if (tabindex < 6) {
this.$router.push({
path: "/centerFull/orderFull/orderlist",
query: {
tabindex
}
});
} else {

console.log(clickUrl)
if(clickUrl!=''){
this.$router.push({//跳路由
path: clickUrl,
query: {
sub_member_id:''
}
});
return false;

}



}
},

async getredpacketinfo(){
let data = {
plat: 3,
account: this.account,
token: this.token
};
const [err, res] = await api.getredpacketinfo(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}else{

if(res.code==2000){
var commonRedpacket = res.data.commonRedpacket;
var thidSixFivePacket = res.data.thidSixFivePacket;
if (commonRedpacket.red_num>0){//普通红包个数大于0的时候
this.isShowModalRedPack=true;
this.redpackBg=commonRedpacket.imgUrl;
this.popName=commonRedpacket.popName;





}
if (thidSixFivePacket.red_num>0){//365红包个数大于0的时候
this.closeModal365=true;
this.fanli365Obj=thidSixFivePacket;





}


}

}
},




















































async catchEnvelopes(e) {
let type_is = e.target.dataset.type;
let data = {
plat: 3,
account: this.account,
token: this.token,
type: type_is
};
const [err, res] = await api.redpacket(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
console.log(res);
if (res.code == "2000") {
console.log(type_is);
if (type_is == 0) {

this.redPacketInfo = res.data;
this.isShowModalRedPack2 = true;
this.isShowModalRedPack = false;
}






}
},

async catchEnvelopes2(e) {
let type_is = e.target.dataset.type;
let data = {
plat: 3,
account: this.account,
token: this.token,
type: type_is
};
const [err, res] = await api.redpacket(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
console.log(res);
if (res.code == "2000") {
console.log(type_is);






if (type_is == 1) {

this.redPacketInfo = res.data;
this.isShowModalRedPack2 = false;
this.closeModal365 = true;
this.successTipTXT=true;
}
}
},

linkToService(type){
let url = '';
switch(type){
case 9:
url = '/index/orderAddress';
break;
case 8:
url = '/centerFull/partner/guize';
break;
case 7:
window.location.href = 'tel://4009639299';
break;
case 6:
break;
case 3:
url = '/centerFull/myService/integral_sc'
break;
case 2:
break;
case 1:
url = '/centerFull/myService/coupon_list';
break;
}
if(!url)return;
console.log(url)
this.$router.push({
path: url,
query: {
from: 'center'
}
})
},

linkTo(link) {
console.log(this.user.user_type);
switch (link) {
case "我的店铺":
if (this.user.user_type == 1) {
this.$router.push('/centerFull/myshop/noShop')

return;
}
this.$router.push("/centerFull/myshop/index");
break;
case "365合伙人":

if (this.user.user_type == 2 || this.user.user_type == 3) {
this.$router.push("/centerFull/partner/inviteList");
} else {

let issmallshop = this.redmessageInfo.is_smallshop;
let store_state = this.redmessageInfo.store_state;

if (issmallshop == 0) {

this.$router.push("/centerFull/partner/code");
if (store_state == 3) {

this.$router.push({
path: "/centerFull/partner/applyStatic",
query: {
status: 1
}
});
return;
} else if (store_state == 4) {

this.$router.push({
path: "/centerFull/partner/applyStatic",
query: {
status: 0
}
});
return;
} else if (store_state == 0) {
this.$router.push("/centerFull/partner/code");
return;
} else {
this.$router.push("/centerFull/partner/code");
return;
}
} else {

alert(2)
this.$router.push("/centerFull/partner/inviteList");
return;
}
}
break;
}
},

linkEarnings() {
this.$router.push("/centerFull/partner/redenvelope");
},

exitLogin() {
if (!this.user) {
this.$router.push("/user/login");
} else {
this.redmessageInfo = "";
this.updateUser("");
this.updateToken("");
this.updateAccount("");
this.updateCenter("");
localStorage.clear();
this.$vux.toast.text("退出成功");
}
},

onCopy() {
this.$vux.toast.text("复制成功", "top");
},
onCopyErr() {
console.log("onCopyErr");
},
...mapActions([
"updateUser",
"updateCenter",
"updateToken",
"updateAccount"
])
},
filters: {
money_fil(val) {
Number(val);
if (val > 9999) {
val = val / 10000 + "万";
}
return val;
}
},
computed: {
...mapGetters(["user", "account", "token"])
}
};



